<template>
  <div class="tabListTen">
    <div class="tabList2One">
      <dl class="tab2OneText">
        <dt>{{tenDt1}}</dt>
        <dd>{{tenDd1}}</dd>
      </dl>
      <div>
        <img src="../../images/case/tabOne5.jpg" alt="">
      </div>
    </div>

    <div class="menu">
      <ModuleMenu
        :ddList="listDd"
      />
    </div>

    <div class="midPic">
      <div class="pic">
        <img src="../../images/case/tabOne8.jpg" alt="">
      </div>
    </div>

    <div class="tabList2Two">
      <div class="tab2TwoText">
        <dl>
          <dt>{{tenDt2}}</dt>
          <dd>{{tenDd2}}</dd>
        </dl>
        <dl>
          <dt>{{tenDt3}}</dt>
          <dd>{{tenDd3}}</dd>
        </dl>
      </div>
      <div class="tab2TwoImg">
        <img src="../../images/case/tabOne6.jpg" alt="">
      </div>
    </div>
    <div class="tabList2Three">
      <div class="tab2ThreeText">
        <dl>
          <dt>{{tenDt4}}</dt>
          <dd>{{tenDd4}}</dd>
        </dl>
        <dl>
          <dt>{{tenDt5}}</dt>
          <dd>{{tenDd5}}</dd>
        </dl>
      </div>
      <div class="tab2ThreeImg">
        <img src="../../images/case/tabOne7.jpg" alt="">
      </div>
    </div>
  </div>
</template>

<script>
  import ModuleMenu from '../../components/module/components/moduleMenu.vue'
  export default {
    props: ['tenDt1','tenDd1','tenDt2','tenDd2','tenDt3','tenDd3','tenDt4','tenDd4','tenDt5','tenDd5'],
    name: "",
    data() {
      return {
        listDd:[{'li':this.$t('business.listDdli1')},{'li':this.$t('business.listDdli2')},{'li':this.$t('business.listDdli3')},{'li':this.$t('business.listDdli4')},{'li':this.$t('business.listDdli5')},{'li':this.$t('business.listDdli6')},{'li':this.$t('business.listDdli7')},{'li':this.$t('business.listDdli8')}]
      }
    },
    components: {
      ModuleMenu
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .tabListTen{
    padding-left: .6rem;
    padding-right: .6rem;
    .tabList2One{
      padding-left: .18rem;
      @include fjb;
      .tab2OneText{
        width: 2.1rem;
        dt{
          @include sc(.28rem,#000);
          padding-bottom: .08rem;
        }
        dd{
          @include sc(.26rem,#000);
          text-align: justify;
        }
      }
      img{
        width: 3.2rem;
        height: 4.3rem;
      }
    }
    .tabList2Two{
      padding-top: .66rem;
      padding-bottom: .5rem;
      .tab2TwoText{
        padding-bottom: .5rem;
        padding-left: 1.4rem;
        dl{
          padding-bottom: .32rem;
          dt{
            @include sc(.28rem,#000);
            padding-bottom: .08rem;
          }
          dd{
            @include sc(.26rem,#000);
            text-align: justify;
          }
        }
      }
      .tab2TwoImg{
        img{
          width: 5rem;
          height: 3.3rem;
        }
      }
    }
    .tabList2Three{
      .tab2ThreeText{
        padding-left: .3rem;
        padding-bottom: .5rem;
        dl{
          padding-bottom: .32rem;
          dt{
            @include sc(.28rem,#000);
            padding-bottom: .08rem;
          }
          dd{
            @include sc(.26rem,#000);
            text-align: justify;
          }
        }
      }
      .tab2ThreeImg{
        display: flex;
        justify-content: flex-end;
        img{
          width: 4.5rem;
          height: 3rem;
        }
      }
    }
    .midPic{
      @include fjc;
      .pic{
        height: 3rem;
        width: 7rem;
        background: red;
        img{
          @include wh(100%,100%)
        }
      }
    }
  }
</style>



