<template>
  <div class="case">
    <mHeader goBack="true" ref="child1" :head-title="$t('business.topTitle')"></mHeader>
    <goTop/>
    <div class="caseTop">
      <caseTop :head-top="$t('business.caseTop')"></caseTop>
    </div>
    <div class="caseContent">
      <!--tab-->
      <van-tabs :active="active" class="caseVantTab">
        <van-tab>
            <div slot="title">
              <goodsTitle :goods-title="$t('other.goods')"></goodsTitle>
            </div>
          <div class="tabList">
            <div class="case1">
              <ModuleOne
                :titleDt="$t('business.cititleDt')"
                :titleDd="$t('business.cititleDd')"
                :titleSpan="$t('business.cititleSpan')"
                d1="true"
              />
            </div>
            <div class="case3">
              <ModuleThree
                :threeP="$t('business.cithreeP')"
                :threeSpan="$t('business.cithreeSpan')"
                d2="true"
              />
            </div>
          </div>
        </van-tab>
        <van-tab>
          <div slot="title">
            <goodsTitle :goods-title="$t('other.details')"></goodsTitle>
          </div>
          <div class="tabList2">
            <div class="case10">
              <ModuleTen
                :tenDt1="$t('business.citenDt1')"
                :tenDd1="$t('business.citenDd1')"
                :tenDt2="$t('business.citenDt2')"
                :tenDd2="$t('business.citenDd2')"
                :tenDt3="$t('business.citenDt3')"
                :tenDd3="$t('business.citenDd3')"
                :tenDt4="$t('business.citenDt4')"
                :tenDd4="$t('business.citenDd4')"
                :tenDt5="$t('business.citenDt5')"
                :tenDd5="$t('business.citenDd5')"
              />
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
    <mFooter/>
  </div>
</template>

<script>
  import mHeader from '../../../components/header/headerTab.vue'
  import caseTop from '../../../components/caseTopScreen.vue'
  import goodsTitle from '../../../components/goods.vue'
  import mFooter from '../../../components/footer/footer.vue'

  import ModuleOne from "../../../components/module/moduleOne.vue"
  import ModuleThree from "../../../components/module/ModuleThree.vue"
  import ModuleTen from "../../../components/module/ModuleTen.vue"
  import goTop from '../../../components/goTop.vue';

  export default {
    name: 'page-tab-container',
    data() {
      return {
        active: 0,
        tabs:['商品','详情'],
        tabContents:['内容1','内容2'],
        num:0,
      };
    },
    mounted () {
      window.addEventListener('scroll', this.handleScroll)
      this.handleScroll();
    },
    destroyed () {
      // 离开页面移除
      window.removeEventListener('scroll', this.handleScroll)
    },
    methods:{
      handleClick(tab, event) {
        console.log(tab, event);
      },
      handleScroll() {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        let opcaity = (scrollTop / 100 > 1) ? 1 : scrollTop / 100;
        this.$refs.child1.headerBac(opcaity);
      },
      tab(index){
        this.num = index;
      }
    },
    components: {
      mHeader,
      mFooter,
      caseTop,
      goodsTitle,
      ModuleOne,
      ModuleThree,
      ModuleTen,
      goTop
    }
  }
</script>
<style lang="scss" scoped>
  @import "../../../style/mixin";
  .case{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 102;
    background: #070705;
    .caseTop{
      padding-top: .88rem;
      width: 100%;
    }
    .caseContent{
      .caseVantTab{
        .tabList{
          background: #fff;
          .case1{
            padding-top: 2rem;
          }
          .case3{
            padding-top: 1.2rem;
            padding-bottom: .8rem;
          }
        }
        .tabList2{
          background: #fff;
          .case10{
            padding-bottom: .8rem;
            padding-top: 1.82rem;
          }
        }
      }

      /*.tabs{
        display: flex;
        justify-content: space-around;
        align-content: center;
        background: #000;
        li{
          @include sc(.32rem,#fff);
          height: 1.1rem;
          line-height: 1.1rem;
          width: 100%;
          text-align: center;
        }
        .tabActive{
          border-bottom: solid 3px #d4be8c;
          color: #d4be8c;
        }
      }
      .tabCon{
        margin-top: 1rem;
        padding-bottom: .8rem;
      }*/
    }
  }

</style>



